import * as React from "react"
import LinkNewTab from "../../../../../components/linkNewTab"
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';

const apiCalls = () => {
    return <main>
        <title>API calls</title>
        <p>The gatsby node api calls can be tracked in source code by looking for <code>apiRunnerNode(</code></p>
        <p>When gatsby needs to run a node api function it is the <LinkNewTab to="https://github.com/gatsbyjs/gatsby/blob/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/utils/api-runner-node.js#L446" title="apiRunnerNode"/> that provides the logic.</p>
        <p>Simplifying a little, for all plugins that implement the node function, gatsby-node.js is <LinkNewTab to="https://github.com/gatsbyjs/gatsby/blob/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/utils/api-runner-node.js#L255" title="is required"/> ( and the plugin is cached ).</p>
        <p>The redux actions ( action creators ) that will be made available to the api function are bound which means their return value is automatically dispatched.  If they return a function then that function is passed the dispatch function.  Of the public actions the createNode action is of this type and will use the dispatch function.  More on this in a moment.</p>
        <p>From redux ( omitting non essential code ) - <LinkNewTab to="https://github.com/reduxjs/redux/blob/d794c56f78eccb56ba3c67971c26df8ee34dacc1/src/bindActionCreators.ts#L58" title="bindActionCreators"/></p>
        <SyntaxHighlighter language="javascript" style={docco}>
  {`
  export default function bindActionCreators(
    actionCreators: ActionCreator<any> | ActionCreatorsMapObject,
    dispatch: Dispatch
  ) {
    if (typeof actionCreators === 'function') {
      return bindActionCreator(actionCreators, dispatch)
    }
    const boundActionCreators: ActionCreatorsMapObject = {}
    for (const key in actionCreators) {
      const actionCreator = actionCreators[key]
      if (typeof actionCreator === 'function') {
        boundActionCreators[key] = bindActionCreator(actionCreator, dispatch)
      }
    }
    return boundActionCreators
  }

  function bindActionCreator<A extends AnyAction = AnyAction>(
    actionCreator: ActionCreator<A>,
    dispatch: Dispatch
  ) {
    return function (this: any, ...args: any[]) {
      return dispatch(actionCreator.apply(this, args))
    }
  }
  `}
</SyntaxHighlighter>
<p>It is redux-thunk that provides action functions - <LinkNewTab to="https://github.com/reduxjs/redux-thunk/blob/290acf90fa5afac5b49f286bb3d8fc51aa864ed3/src/index.js#L4" title="see"/> </p>
<p>Then the bound action creators are then double bound.  The action creators can take 1-3 arguments.  The  third argument is not provided by the caller but is the same <b>api specific</b> "arguments" object that the api function will receive as its first argument.
  If an action creator takes two arguments then the second argument is a plugin argument.  If you do not specify then the plugin is the plugin being run.  From the code comments this functionality is limited and you would need to understand the action.</p>
  <SyntaxHighlighter language="javascript" style={docco}>
  {`
          doubleBoundActionCreators[key] = (...args) => {
            // Let action callers override who the plugin is. Shouldn't be
            // used that often.
            if (args.length === 1) {
              return boundActionCreator(args[0], plugin, actionOptions)
            } else if (args.length === 2) {
              return boundActionCreator(args[0], args[1], actionOptions)
            }
            return undefined
          }
  `}
</SyntaxHighlighter>
<p>Then the node api function is invoked with two arguments.  The first argument is an object with fields that are common for all api functions and ones that are particular to it <LinkNewTab to="https://github.com/gatsbyjs/gatsby/blob/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/utils/api-runner-node.js#L375" title="fields that are common for all api functions and ones that are particular to it"/>.  The second argument is the plugin's options.  A node api function can have as a third argument a callback, of course returning a promise is better.</p>
<p>The firt argument is known as in the docs as <LinkNewTab to="https://www.gatsbyjs.com/docs/reference/config-files/node-api-helpers/" title="node api helpers"/>.  Some of these are described as internal such as the emitter and the redux store.  To see the slices available from the store see <LinkNewTab to="https://github.com/gatsbyjs/gatsby/tree/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/redux/reducers" title="redux reducers folder"/>.</p>
<p>The emitter emits named events with the emiyt method.  What this means is that all handlers that have subscribed to the event name will be notified.  It is also possible to subscribe to all events using <LinkNewTab to="https://github.com/gatsbyjs/gatsby/blob/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/utils/mett.ts#L47" title="*"/>.</p>
<p>The emitter emits whenever the <LinkNewTab to="https://github.com/gatsbyjs/gatsby/blob/492068b0d1a08e544c3c51fb2fc1360087437b99/packages/gatsby/src/redux/index.ts#L153" title="redux store has been updated"/></p>
</main>
}

export default apiCalls